.wrap {
    display: flex;
    justify-content: center;
    background-color: #f6f6f6;
    .content {
        display: flex;
        flex-direction: column;
        padding-top: 40px;
        width: 1440px;
        .banner {
            display: flex;
            justify-content: space-between;
            max-width: 100%;
            height: 488px;
            border-radius: 20px;
            background: linear-gradient(
                100deg,
                rgba(245, 204, 160, 1) 0%,
                rgba(228, 143, 69, 1) 40%
            );
            overflow: hidden;
            img:first-child {
                margin-left: 30px;
                object-fit: cover;
                height: 150%;
                animation: moveImage 1.5s infinite alternate;
                z-index: 1;
                @keyframes moveImage {
                    0% {
                        transform: rotate(4deg);
                    }
                    100% {
                        transform: rotate(-6deg);
                    }
                }
            }
            img:nth-child(2) {
                align-self: flex-end;
                height: 30%;
                animation: moveOto 5s ease-in-out infinite;
                @keyframes moveOto {
                    0% {
                        transform: translateX(180%);
                        -webkit-transform: translateX(180%);
                        opacity: 0;
                    }
                    50% {
                        transform: translateX();
                        -webkit-transform: translateX();
                        opacity: 1;
                    }
                    100% {
                        transform: translateX(-420%);
                        -webkit-transform: translateX(-420);
                        opacity: 0;
                    }
                }
                @-webkit-keyframes moveOto {
                    0% {
                        -webkit-transform: translateX(180%);
                        -webkit-opacity: 0;
                    }
                    50% {
                        -webkit-transform: translateX();
                        -webkit-opacity: 1;
                    }
                    100% {
                        -webkit-transform: translateX(-420);
                        -webkit-opacity: 0;
                    }
                }
            }
            img:last-child {
                height: 50%;
                align-self: flex-end;
            }
        }
        .video-lessons,
        .text-lessons {
            min-height: 445px;
            margin-top: 40px;
            h2 {
                margin-bottom: 20px;
                color: var(--text-color);
                font-size: 24px;
                font-weight: 700;
                line-height: 34px;
            }
            .wrap {
                display: flex;
                justify-content: space-around;
                flex-wrap: wrap;
                padding: 20px 0;
                border-radius: 20px;
                background-color: #fff;
                .lessons {
                    display: flex;
                    width: 413px;
                    height: 156px;
                    margin: 10px 0;
                    padding: 20px 26px 20px 20px;
                    border-radius: 16px;
                    background-color: var(--primari-color);
                    &:hover {
                        cursor: pointer;
                        opacity: 0.8;
                    }
                    img {
                        border-radius: 10px;
                        width: 116px;
                        height: 116px;
                    }
                    .title {
                        margin-left: 20px;
                        h3 {
                            color: var(--text-color);
                            font-size: 16px;
                            font-weight: 700;
                            line-height: 24px;
                        }
                        p {
                            margin-top: 10px;
                            color: var(--text-color);
                            font-size: 16px;
                            font-weight: 400;
                            line-height: 24px;
                        }
                    }
                }
            }
        }
        .text-lessons {
            margin-top: 40px;
        }
    }
}

@media (min-width: 0px) and (max-width: 739px) {
    .wrap {
        .content {
            padding-top: 20px;
            height: 100%;
            min-width: 375px;
            .banner {
                height: 156px;
            }
            .video-lessons,
            .text-lessons {
                margin-top: 20px;
                height: 524px;
                h2 {
                    font-size: 20px;
                    margin-bottom: 10px;
                }
                .wrap {
                    margin: 0;
                    padding: 0;
                    height: 464px;
                    overflow-x: auto;
                    .lessons {
                        width: 80%;
                    }
                }
            }
        }
    }
}
